@import url(https://fonts.googleapis.com/css?family=Kosugi+Maru&display=swap);
body{margin:0;padding:0;font-family:Kosugi Maru;font-size:10px}table.table-bordered{width:95vw;padding:1}.table>tbody>tr>td,.table>tbody>tr>th,.table>tfoot>tr>td,.table>tfoot>tr>th,.table>thead>tr>td,.table>thead>tr>th{padding:4px}td{border:solid 1px #ddd;padding:2px;text-align:right;white-space:nowrap}td.total_100{background:#fc1;background:linear-gradient(45deg, #fc1 0%, #ffe 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='#fc1', endColorstr='#ffe',GradientType=1 )}td.date{max-width:10em}td.total_over99{background-color:#ffa}td.total_over98{background-color:#cfc}td.total_over95{background-color:#aff}td.total_over90{background-color:#ccf}td.total_over85{background-color:#fbf}td.total_over80{background-color:#fcd}td.songName{max-width:150px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;text-align:left}.tableInModal td{border:none}th{text-align:center}.modal-content{background-color:#222222;color:white}.score{font-size:32px;font-weight:bold;text-shadow:3px 3px 0 #cc1bcc}.average{color:yellow;font-weight:bold}.circle{text-shadow:1px 1px 0 #1aaaa3,-1px 1px 0 #1aaaa3,1px -1px 0 #1aaaa3,-1px -1px 0 #1aaaa3}.modal-dialog{position:absolute;top:50%;left:46%;transform:translate(-50%, -50%) !important}

